import React from "react"

function TodoItem(props) {
    const completedstyles = {
        ontStyle: "italic",
        color: "#cdcdcd",
        textDecoration: "line-through"
    }

    const todayDate = new Date()
    const taskDate = new Date(props.item.date)
    console.log("task date: ", taskDate)
    let messageOnTime;
    if(taskDate.getDay() === todayDate.getDay() && taskDate.getMonth() === todayDate.getMonth() && taskDate.getFullYear() === taskDate.getFullYear()){
        
        console.log("today date: ", todayDate)
        console.log(props.item.date)
        console.log("task name: ",props.item.text)
        messageOnTime="Due Today"
    }else if (taskDate.getDate() !== todayDate.getDate()){
        if(taskDate.getMonth() === todayDate.getMonth() && taskDate.getFullYear() === taskDate.getFullYear()){
            console.log("same month and year")
            let daysLeft = taskDate.getDay() - todayDate.getDay()
            if(daysLeft>0){
                if(daysLeft ===1){
                    messageOnTime= "Due tomorrow"
                }
                else if(daysLeft >1){
                    messageOnTime= "Due in " +daysLeft + " day(s)"
                }
                
            }else if(daysLeft<0){
                messageOnTime= "Overdue"
            }
        }else if(taskDate.getFullYear() === taskDate.getFullYear() && taskDate.getMonth() > todayDate.getMonth() ){
            let monthsLeft = taskDate.getMonth() - todayDate.getMonth()
            let daysLeft = taskDate.getDay() - todayDate.getDay()
            if(monthsLeft>0){
                if(monthsLeft ===1){
                    if(daysLeft >0){
                        if(daysLeft ===1){
                            messageOnTime = "Due tomorrow"
                        }else{
                            messageOnTime = "Due in " + daysLeft + "day(s)"
                        }
                        
                    }
                    messageOnTime = "Due next month"
                }else if(monthsLeft >1){
                    messageOnTime= "Due in " + monthsLeft + " month(s)" 

                }
                
            }else{
                messageOnTime= "Overdue"
            }
        }

    }

    return (
            <tr>
                <td><input
                        type="checkbox"
                        checked={props.item.completed}
                        onChange={() => props.handleChange(props.item.id)}
                    /></td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.text}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.timeRequired}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.date}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{messageOnTime}</td>
                <td style={props.item.completed === true ? completedstyles : null}>{props.item.priority}</td>
                <td><button type="button" onClick={() => props.Deletion(props.item.id)}>Delete</button></td>
            </tr>
    )
}

export default TodoItem