import React from "react";

function PopForm(props) {
    return (
        <div>
            <form className="form-style-10">
            <button type="button" className="close" aria-label="Close" onClick={props.handleCancel}>
            <span aria-hidden="true">&times;</span>
            </button><br/>
                TO-DO task:<br/><br/>
                <input type="text" name="taskName" onChange={props.handleTEXT}/><br/>
                Priority:
                <select className = "form-style-10 select" name="priority" value={props.tempPrio} onChange={props.handleTEXT}>
                <option value="Highest">Highest</option>
                <option value="High">High</option>
                <option value="Medium">Medium</option>
                <option value="Low">Low</option>
                </select>
                Estimation of hours to complete:
                <input  type="number" className= "form-style-10 textarea" name="quantity" min="1" max="9999" onChange={props.handleTEXT}/>
                Due date:<br/>
                <input type="date"  className= "form-style-10 textarea" name="dueDate" onChange={props.handleTEXT}/>
                <input type="button"  className= "form-style-10 textarea" value="Submit" onClick={props.AddingElem}/>
                <input type="button"  className= "form-style-10 textarea" value="Cancel" onClick={props.handleCancel}/>          
            </form>

        </div>
            )
        
        
}

export default PopForm